import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import About from './views/About.vue'
import Group from './views/Groupoverview.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/home',
      name: 'home',
      component: Home
    },
    {
      path: '/about',
      name: 'about',
      component: About
    },
    {
      path: '/group',
      name: 'group',
      component: Group
    },
    {
      path: '/twitter',
      beforeEnter() {location.href = 'https://twitter.com/ImmanuelLeaders?s=08'}
    }
  ]
})
