<template>
    <nav>
        <v-toolbar app dark class="grey darken-3">
        <v-toolbar-side-icon @click.stop="drawer = !drawer" class="hidden-md-and-up"></v-toolbar-side-icon>
        <v-avatar><v-img :src="require('../assets/logo.jpg')" max-height="50" max-width="50" class="fab"></v-img></v-avatar>
        <v-toolbar-title>IMMANUEL LEADERS</v-toolbar-title>

        <v-spacer class="hidden-sm-and-down"></v-spacer>
        <v-btn flat class="hidden-sm-and-down" color="amber darken-3" v-for="(link, i) in  links" :key="i" router :to="link.route">
            <span class="white--text font-weight-thin">{{link.text}}</span>
        </v-btn>
        <v-menu transition="slide-y-transition">
            <template v-slot:activator="{ on }">
                <v-btn v-on="on" flat color="amber darken-3 white--text" class="hidden-sm-and-down"><span class="white--text font-weight-thin">Contact Us</span> <v-icon>arrow_drop_down</v-icon></v-btn>
            </template>
            <v-list>
                <v-list-tile v-for="(item, i) in items" :key="i" :href="item.route">
                    <v-list-tile-title>{{item.text}}</v-list-tile-title>
                </v-list-tile>
            </v-list>
        </v-menu>
        
        </v-toolbar>

        <v-navigation-drawer v-model="drawer" app dark class="grey darken-3 hidden-md-and-up">
            <v-list>
                <v-list-tile avatar>
                    <v-list-tile-avatar>
                        <img src="../assets/logo.jpg">
                    </v-list-tile-avatar>
                    <v-list-tile-content>
                        <v-list-tile-title>Immanuel Leaders</v-list-tile-title>
                    </v-list-tile-content>
                </v-list-tile>
                <v-divider></v-divider>
                <v-list-tile v-for="(link, i) in drawer_list" :key="i" router :to="link.route">
                    <v-list-tile-action>
                        <v-icon>{{link.icon}}</v-icon>
                    </v-list-tile-action>
                    <v-list-tile-content>
                        <v-list-tile-title>{{link.text}}</v-list-tile-title>
                    </v-list-tile-content>
                </v-list-tile>

                <v-list-tile avatar>
                    <v-expansion-panel>
                        <v-expansion-panel-content inset>
                            <div slot="header">Contact Us</div>
                            <v-card v-for="(item, index) in items" :key="index">
                                <a class="link" :href="item.route">
                                    <v-card-text class="subheading link">
                                       <v-icon>{{item.icon}}</v-icon> {{item.text}}
                                    </v-card-text>
                                </a>
                                
                                <v-divider></v-divider>
                            </v-card>
                        </v-expansion-panel-content>
                    </v-expansion-panel>
                </v-list-tile>
            </v-list>
        </v-navigation-drawer>
    </nav>
</template>

<script>
export default {
    data(){
        return{
            drawer: false,
            items:[
                {text: 'Facebook', route: "#", icon: "fab fa-facebook"},
                {text: 'Twitter', route: "https://twitter.com/ImmanuelLeaders?s=08", icon: "fab fa-twitter"},
                {text: 'Instagram', route: "#", icon: "fab fa-instagram"}
            ],
            links: [
                {text: "Home", route: "/home"},
                {text: "About", route: "/about"},
                {text: "Group overview", route: "/group"}
            ],
            drawer_list: [
                {text: "Home", icon: "home", route: "/home"},
                {text: "About", icon: "question_answer", route: "/about"},
                {text: "Group overview", icon: "group", route: "/group"}
            ]
        }
    },
    computed: {
        show(){
            if(window.innerHeight > 960){
                return false
            }else return true
        }
    },
}
</script>

<style>
    .v-card__text.link{
        text-decoration: none;
        color: white;
    }
    a.link{
        text-decoration: none;
    }
</style>

